@import url(https://fonts.googleapis.com/css?family=Poiret+One);

@import url(https://cdnjs.cloudflare.com/ajax/libs/weather-icons/2.0.9/css/weather-icons.min.css);

* {
  margin: 0;
  padding: 0;

}

body{
  background: url(../../public/image.png);
background-size: 100vw 100vh;
background-repeat: no-repeat;
height: 100vh;
display: fixed;
}
#root{
  width: 100%;
  display: flex;
justify-content: center;
display: fixed;
}
.heading{
  font-family: 'Anek Latin', sans-serif;
  text-align: center;
  color: rgba(40, 38, 37, 0.717);
}
.main{
 margin: 0%;

height: 100%;

}
.searchBar{
margin: 0%;
margin-top: 130px;
}
 .searchBox {
height: 36px;
width: 350px;
margin: 0%;
font-size: larger;
border-radius: 9px 0px 0px 9px;
border: 0ch;
padding: 3px;
margin-left: 22px;
}

.searchButton{
 height: 42px;
 padding-top: 4px;
 background: rgb(104, 188, 19);
 color: black;
 border-radius: 0px 9px 9px 0px;
 width: 90px;
}

.section{
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  height: 55vh;

  border-radius: 25px;
  overflow: hidden;
  background-color: aliceblue;
  
  
  }
  
  
.weatherPhoto{
  background-color: rgb(46, 160, 160);
  height: 30%;
  display: flex;
  align-items: center;
  justify-content: center;
  border-bottom:  solid black;
}
.icons{
    
     min-width: 50px;
     height: 50px;
  }
.weatherInfo
{
  background: rgb(40, 42, 47);
  height:51% ;
  display: flex;
  flex-direction: row;

}
.timeInfo{
  width: 35%;
  position: relative;
  text-align: center;
}
.tempInfo{
  background-color:rgb(46, 160, 160);
  width: 65%;
  border-right: 2px solid black;
  position: relative;
  text-align: center;
}
 .timeInfo h2{
  position: absolute;
  top: 30%;
  color: azure; 
  font-family: 'Anek Malayalam', sans-serif;
 }
 .tempInfo h1,h2,h3{
  position: relative;
  top: 35%;
font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  color: azure;  

 }
.extraInfo{
  background: rgb(46, 160, 160);
  height: 19%;
  display: flex;
  border-top: 2px black solid;
}
.extraPhoto{
  display: flex;
  margin-top: 8px;
  margin-left: 5px;
  margin-right: 9px;
}
.extraPhoto img{
  height: 33px;
  margin-right: 13px;
  color: aliceblue;
}
.extraPhoto p{
  text-align: center;
  color: rgb(227, 202, 202);
  font-family: 'Anek Latin', sans-serif;;
}